import Vue from "vue";
import Router from "vue-router";
import Dashboard  from '@/components/Dashboard.vue';
import Income from '@/components/income/Income.vue';
import Statistics from '@/components/statistics/Statistics.vue';
import UserProfile from '@/components/user-profile/UserProfile.vue';
import Settings from '@/components/settings/Settings.vue';

import Patients from '@/components/patients/App.vue';
import PatientsList from '@/components/patients/Patients.vue';
import Patient from '@/components/patients/Patient.vue';

Vue.use(Router);

export default new Router({
    mode: 'history',
    routes: [
        {
            path: '/',
            name: 'dashboard',
            component: Dashboard
        },
        {
            path: '/patients/',
            component: Patients,
            children: [
                {
                    path: '/',
                    component: PatientsList
                },
                {
                    path: 'new-patient',
                    component: Patient
                },
                {
                    path: 'edit-patient/:id',
                    component: Patient
                }
            ]
        },
        {
            path: '/income/',
            name: 'income',
            component: Income
        },
        {
            path: '/statistics/',
            name: 'statistics',
            component: Statistics
        },
        {
            path: '/user-profile/',
            name: 'user-profile',
            component: UserProfile
        },
        {
            path: '/settings/',
            name: 'settings',
            component: Settings
        }
    ]
})