<template>
  <b-container fluid>
    <b-button-group>
      <b-button variant="success" to="/patients/new-patient" class="mb-3">
        <i class="fa fa-plus"></i> New Patient
      </b-button>
      <Inpatient />
      <LabRequest />
    </b-button-group>

    <b-input-group>
      <b-input-group-text slot="prepend">
        <i class="fa fa-search"></i>
      </b-input-group-text>
      <b-form-input placeholder="Search name"></b-form-input>
    </b-input-group>

    <b-card
      no-body
      class="overflow-hidden"
      v-for="(patient, index) in patients"
      :key="index"
      :title="patient.Name"
      border-variant="success"
      :style="{'background-color': index % 2 === 0 ? '' : '#edfbf0'}"
    >
      <b-row no-gutters>
        <b-col md="1">
          <b-card-img :src="patient.ImageSrc" v-if="patient.ImageSrc"></b-card-img>
          <b-card-img :src="defaultImageSrc" v-else height="100%"></b-card-img>
        </b-col>
        <b-col md="11">
          <b-button
            variant="outline-secondary"
            @click="edit(patient.Id)"
            title="Edit patient"
            style="float:right; margin-top:5px; margin-right:5px"
          >
            <i class="fa fa-edit"></i>
          </b-button>
          <b-card-body body-border-variant="secondary">
            <b-container fluid>
              <b-container fluid>
                <b-row>
                  <b-col sm="2">
                    <label for="input-small" class="label-font">Name : {{patient.Name}}</label>
                  </b-col>
                  <b-col sm="1">
                    <label for="input-small" class="label-font">Age : {{patient.Age}}</label>
                  </b-col>
                  <b-col sm="4">
                    <label for="input-small" class="label-font">Address : {{patient.Address}}</label>
                  </b-col>
                  <b-col sm="2">
                    <label
                      for="input-small"
                      class="label-font"
                    >Cellphone # : {{patient.PersonalPhoneNumber}}</label>
                    <!-- <b-button-group v-if="patient.PersonalPhoneNumber">
                      <b-button size="sm" variant="secondary" title="Call">
                        <i class="fa fa-phone"></i>
                      </b-button>
                      <b-button size="sm" variant="secondary" title="Sms">
                        <i class="fa fa-envelope"></i>
                      </b-button>
                    </b-button-group>-->
                  </b-col>
                  <b-col sm="2">
                    <label
                      for="input-small"
                      class="label-font"
                    >Email Address : {{patient.EmailAddress}}</label>
                    <br />
                  </b-col>
                </b-row>
              </b-container>
            </b-container>
          </b-card-body>
        </b-col>
      </b-row>
    </b-card>
    <Pagination :data="rawData" :properties="paginationData" v-show="rawData.length" />
    <span hidden>{{pageWacher}}</span>
  </b-container>
</template>
<script>
import Inpatient from "./In-patient.vue";
import LabRequest from "./LaboratoryRequest.vue";
import Pagination from "../common/Pagination.vue";
import patients from "../../mock-data/patients";
export default {
  name: "patients",
  components: {
    Pagination,
    Inpatient,
    LabRequest
  },
  data() {
    return {
      paginationData: {
        rpp: 10,
        page: 1
      },
      patients: [],
      rawData: patients,
      defaultImageSrc: "../assets/default-image.png"
    };
  },
  methods: {
    edit: function(id) {
      this.$router.push({
        path: `/patients/edit-patient/${id}`
      });
    },
    loadPerPageData: function() {
      let { page, rpp } = this.paginationData;
      let offset = (page - 1) * rpp;
      let result = this.rawData.slice(offset, rpp + offset);
      this.patients = result;
    }
  },
  computed: {
    pageWacher: function() {
      if (this.paginationData.page) {
        this.loadPerPageData();
        return this.paginationData.page;
      }
    }
  },
  created() {
    this.loadPerPageData();
  }
};
</script>

<style scoped>
.label-font {
  font-size: 14px;
}
</style>