<template>
  <b-button variant="secondary" @click="$bvModal.show('modal-in-patient')" class="mb-3" >
    <b-modal id="modal-in-patient" :no-close-on-backdrop="true" :centered="true" hide-footer>
      <template slot="modal-header">
        <h5>In-Patient</h5>
        <b-button size="sm" variant="outline-danger" @click="cancel">
          <i class="fa fa-times"></i>
        </b-button>
      </template>

      <template slot="default">
        <b-container fluid>
          <b-form @submit="save" @reset="cancel">
            <b-form-group label="Type:">
              <b-form-select v-model="inPatient.type" :options="types" @change="selectTypes(inPatient.type)" required></b-form-select>
            </b-form-group>
            <b-form-group label="HMO Type:" v-if="inPatient.isHMO === true">
              <b-form-select
                v-model="inPatient.Name"
                :options="hmoTypes"
                @change="selectedHMOType(inPatient.Name)"
                required
              ></b-form-select>
            </b-form-group>
            <b-form-group label="Name:">
              <b-form-input v-model="inPatient.Name" required placeholder="Name"></b-form-input>
            </b-form-group>
            <b-form-group label="Amount:">
              <b-form-input v-model="inPatient.amount" required placeholder="Amount"></b-form-input>
            </b-form-group>
            <hr />
            <b-button-group>
              <b-button variant="success" type="submit">Submit</b-button>
              <b-button variant="danger" type="reset">Cancel</b-button>
            </b-button-group>
          </b-form>
        </b-container>
      </template>
    </b-modal>
    <i class="fa fa-wheelchair"></i> In Patient
  </b-button>
</template>
<script>
const initInPatient = {
  Name: null,
  type: "PAYING",
  amount: null,
  isHMO: false
};

export default {
  data() {
    return {
      types: ["PAYING", "HMO", "PHILHEALTH CHECK", "PHILHEALTH LANDBANK"],
      hmoTypes: ["MAXICARE", "CLINICCARD", "INTELLICARE", "MKAVI"],
      inPatient: Object.assign({}, initInPatient)
    };
  },
  methods: {
    selectTypes: function(type) {
      this.inPatient.isHMO = false;
      this.inPatient.Name = "";
      if (type === "HMO") {
        this.inPatient.isHMO = true;
      } else if (type === "PAYING") {
        // do nothing
      } else {
        this.inPatient.Name = type;
      }
    },
    selectedHMOType: function(name) {
      this.inPatient.Name = name;
    },
    save: function() {
      this.hide();
    },
    cancel: function() {
      this.hide();
    },
    hide: function() {
      this.$bvModal.hide("modal-in-patient");
      this.inPatient = Object.assign({}, initInPatient);
    }
  }
};
</script>