<template>
  <b-container fluid>
    <b-button variant="secondary" to="/patients/" class="mb-3">Back</b-button>
    <hr />
    <b-form @submit="onSubmit" @reset="onReset">
      <notification
        v-if="messageNotif"
        :dismissCountDown="dismissCountDown"
        :message="messageNotif"
        :variant="variant"
      />
      <b-form-group label-size="lg" label="Patient Information">
        <b-row class="mt-3">
          <b-col cols="12" md="4">
            <b-form-group>
              <b-card-img
                height="370px"
                :src="patientPhoto.tempPhoto"
                v-if="patientPhoto.tempPhoto"
                :bottom="true"
              ></b-card-img>
              <b-card-img :src="patientPhoto.path" v-else></b-card-img>
              <b-form-file
                accept="image/jpeg, image/png, image/gif"
                v-model="patientPhoto.tempPhoto"
                placeholder="Choose a file..."
                drop-placeholder="Drop file here..."
                @change="setTemporaryImage($event)"
              ></b-form-file>
            </b-form-group>
          </b-col>
          <b-col cols="12" md="8">
            <b-row>
              <b-col cols="12" md="6">
                <b-form-group label="First Name:">
                  <b-form-input v-model="patient.firstName" required placeholder="First Name"></b-form-input>
                </b-form-group>
                <b-form-group label="Last Name:">
                  <b-form-input v-model="patient.lastName" required placeholder="Last Name"></b-form-input>
                </b-form-group>
                <b-form-group label="Gender:">
                  <b-form-select v-model="patient.gender" :options="genders" required></b-form-select>
                </b-form-group>
                <b-form-group label="Civil Status:">
                  <b-form-select v-model="patient.civilStatus" :options="civilStatuses" required></b-form-select>
                </b-form-group>
                <b-form-group label="Telephone Number:">
                  <b-form-input
                    v-model="patient.homePhoneNumber"
                    placeholder="088-XXX-XXXX"
                    v-mask="'###-###-####'"
                  ></b-form-input>
                </b-form-group>
              </b-col>
              <b-col cols="12" md="6">
                <b-form-group label="M.I.:">
                  <b-form-input v-model="patient.middleInitial" placeholder="Middle Initial"></b-form-input>
                </b-form-group>
                <b-form-group label="Date of Birth:">
                  <date-picker
                    v-model="patient.dateOfBirth"
                    :config="options"
                    placeholder="DD/MM/YYYY"
                    v-mask="'##/##/####'"
                  ></date-picker>
                </b-form-group>
                <b-form-group label="Email address:">
                  <b-form-input
                    v-model="patient.emailAddress"
                    type="email"
                    required
                    placeholder="Email Address"
                  ></b-form-input>
                </b-form-group>
                <b-form-group label="Cellphone Number:">
                  <b-form-input
                    v-model="patient.personalPhoneNumber"
                    placeholder="09XX-XXX-XXX"
                    v-mask="'09##-###-####'"
                  ></b-form-input>
                </b-form-group>
              </b-col>
            </b-row>
          </b-col>
          <b-col cols="12" md="12">
            <b-form-group label="Home Address:">
              <b-form-input v-model="patient.Address" required placeholder="Home Address"></b-form-input>
            </b-form-group>
          </b-col>
        </b-row>
      </b-form-group>
      <hr />
      <b-form-group
        v-for="(parent, index) in parents"
        :key="parent.id"
        label-size="lg"
        :label="parentsDiv[index]"
      >
        <b-row class="mt-3">
          <b-col cols="12" md="4">
            <b-form-group label="First Name:">
              <b-form-input v-model="parent.firstName" required placeholder="First Name"></b-form-input>
            </b-form-group>
          </b-col>
          <b-col cols="12" md="4">
            <b-form-group label="Last Name:">
              <b-form-input v-model="parent.lastName" required placeholder="Last Name"></b-form-input>
            </b-form-group>
          </b-col>
          <b-col cols="12" md="4">
            <b-form-group label="M.I:">
              <b-form-input v-model="parent.middleInitial" placeholder="Middle Initial"></b-form-input>
            </b-form-group>
          </b-col>
        </b-row>
        <b-row class="mt-3">
          <b-col cols="12" md="4">
            <b-form-group label="Age:">
              <b-form-input v-model="parent.age" required placeholder="Age"></b-form-input>
            </b-form-group>
          </b-col>
          <b-col cols="12" md="4">
            <b-form-group label="Occupation">
              <b-form-input v-model="parent.occupation" placeholder="Occupatione"></b-form-input>
            </b-form-group>
          </b-col>
        </b-row>
        <hr />
      </b-form-group>

      <b-button-group>
        <b-button type="submit" variant="success">Submit</b-button>
        <b-button type="reset" variant="danger">Reset</b-button>
      </b-button-group>
    </b-form>
  </b-container>
</template>
<script>
// Import required dependencies
import "bootstrap/dist/css/bootstrap.css";

// Import this component
import datePicker from "vue-bootstrap-datetimepicker";

// Import date picker css
import "pc-bootstrap4-datetimepicker/build/css/bootstrap-datetimepicker.css";

// Import notification alert
import notification from "../common/Notification.vue";

const initPatient = {
  id: null,
  firstName: null,
  lastName: null,
  middleInitial: null,
  gender: null,
  age: null,
  dateOfBirth: new Date(),
  address: null,
  civilStatus: null,
  personalPhoneNumber: null,
  homePhoneNumber: null,
  emailAddress: null
};

const initParents = [
  {
    id: 1,
    firstName: null,
    lastName: null,
    middleInitial: null,
    age: null,
    occupation: null
  },
  {
    id: 2,
    firstName: null,
    lastName: null,
    middleInitial: null,
    age: null,
    occupation: null
  }
];
export default {
  components: {
    datePicker,
    notification
  },
  data() {
    return {
      options: {
        format: "DD/MM/YYYY",
        useCurrent: false
      },
      genders: [{ text: "Select", value: null }, "Male", "Female"],
      civilStatuses: [
        { text: "Select", value: null },
        "Single",
        "Married",
        "Widow",
        "Separated"
      ],
      patient: Object.assign({}, initPatient),
      parentsDiv: ["Father's Information", "Mother's Information"],
      parents: Object.assign({}, initParents),
      patientPhoto: {
        tempPhoto: "",
        path: "../assets/default-image.png",
        isPrimary: true
      },
      messageNotif: null,
      dismissCountDown: 0,
      variant: "success"
    };
  },
  methods: {
    onSubmit(evt) {
      evt.preventDefault();
      this.dismissCountDown = 0;
      this.messageNotif = "Failed to saved!";
      this.dismissCountDown = 5;
      this.variant = "danger";
      console.log(this.dismissCountDown);
    },
    onReset(evt) {
      evt.preventDefault();
      this.patient = Object.assign({}, initPatient);
      this.parents = Object.assign({}, initParents);
    },
    setTemporaryImage(evt) {
      var input = evt.target;
      if (input.files && input.files[0]) {
        var reader = new FileReader();
        // Define a callback function to run, when FileReader finishes its job
        reader.onload = e => {
          // Note: arrow function used here, so that "this.imageData" refers to the imageData of Vue component
          // Read image as base64 and set to imageData
          this.patientPhoto.tempPhoto = e.target.result;

          console.log(this.patientPhoto);
        };
        reader.readAsDataURL(input.files[0]);
      }
    }
  }
};
</script>
