<template>
  <b-alert
    :show="dismissCountDown"
    :dismissible="isDismissable"
    :variant="variant"
    @dismissed="dismissCountDown=0"
    @dismiss-count-down="countDownChanged"
  >
    <span v-if="showCountDown">
      <p>This alert will dismiss after {{ dismissCountDown }} seconds...</p>
      <b-progress variant="warning" :max="dismissCountMax" :value="dismissCountDown" height="4px"></b-progress>
    </span>
    <span v-else>{{message}}</span>
  </b-alert>
</template>
<script>
export default {
  props: {
    isDismissable: {
      default: true,
      type: Boolean
    },
    variant: {
      default: "success",
      type: String,
      required: true
    },
    dismissCountDown: {
      type: Number
    },
    dismissCountMax: {
        default: 5,
        type: Number
    },
    message: {
      type: String,
      required: true
    },
    showCountDown: {
      default: false,
      type: Boolean
    }
  },
  methods: {
      countDownChanged(dismissCountDown) {
        this.dismissCountDown = dismissCountDown
        if(dismissCountDown === 0) {
            this.message = null;
        }
        console.log(dismissCountDown)
      },
  }
};
</script>