<template>
  <b-pagination v-model="properties.page" :total-rows="rows" :per-page="properties.rpp" small>
    <span class="text-secondary" slot="first-text">First</span>
    <span class="text-secondary"></span>
    <span class="text-secondary"></span>
    <span class="text-secondary" slot="last-text">Last</span>
    <div slot="ellipsis-text">
      <b-spinner small type="grow"></b-spinner>
      <b-spinner small type="grow"></b-spinner>
      <b-spinner small type="grow"></b-spinner>
    </div>
    <span slot="page" slot-scope="{ page, active }" class>
      <b v-if="active">{{ page }}</b>
      <span v-else>{{ page }}</span>
    </span>
  </b-pagination>
</template>
<script>
export default {
  props: {
    data: {
      type: Array,
      required: true
    },
    properties: {
      type: Object,
      required: true
    }
  },
  computed: {
    rows: function() {
      if (this.data && this.data.length) {
        return this.data.length;
      }
    }
  }
};
</script>
<style>
.page-item.active .page-link {
  background-color: #28a745;
  border-color: #28a745;
}

.page-link {
  color: #28a745;
}
</style>