<template>
  <b-container>
    <div class="mt-3">
      <b-card-group columns v-if="dashboards.length">
        <b-card
          v-for="dashboard in dashboards"
          :key="dashboard.Name"
          :header="dashboard.Name"
          header-bg-variant="success"
          header-text-variant="white"
          align="center"
          class="card-border"
        >
          <b-card-text @click.prevent="goTo(dashboard.Path)">
            <img :src="dashboard.Image" :width="dashboard.width" class="card-image" />
          </b-card-text>
        </b-card>
      </b-card-group>
    </div>
  </b-container>
</template>

<script>
export default {
  name: "dashboard",
  data() {
    return {
      dashboards: [
        {
          Name: "Patients",
          Description: "",
          Image: "./assets/patients.png",
          width: "144px",
          Path: "/patients/"
        },
        {
          Name: "Income",
          Description: "",
          Image: "./assets/income.png",
          width: "150px",
          Path: "/income/"
        },
        {
          Name: "Statistics",
          Description: "",
          Image: "./assets/statistics.png",
          width: "150px",
          Path: "/statistics/"
        },
        {
          Name: "Edit Profile",
          Description: "",
          Image: "./assets/edit-profile.png",
          width: "150px",
          Path: "/user-profile/"
        },
        {
          Name: "Settings",
          Description: "",
          Image: "./assets/settings.png",
          width: "150px",
          Path: "/settings/"
        }
      ]
    };
  },
  methods: {
    goTo: function(path) {
      this.$router.push({
        path: path
      });
    }
  }
};
</script>
<style scoped>
.card-image {
  cursor: pointer;
  transition: outline 0.2s linear;
  margin: 0.7em;
}
.card-border:hover { 
  border-color: green;
}
</style>