<template>
  <b-button variant="secondary" @click="$bvModal.show('modal-lab-request')" class="mb-3">
    <b-modal id="modal-lab-request" :no-close-on-backdrop="true" :centered="true" hide-footer>
      <template slot="modal-header">
        <h5>Laboratory/X-ray Request Form</h5>
        <b-button size="sm" variant="outline-danger" @click="cancel">
          <i class="fa fa-times"></i>
        </b-button>
      </template>

      <template slot="default">
        <b-container fluid>
          <b-form @submit="print" @reset="cancel">
            <b-row class="mt-3">
              <b-col cols="12" md="8">
                <b-form-group label="Name:">
                  <b-form-input v-model="labRequest.name" required placeholder="Name"></b-form-input>
                </b-form-group>
                <b-form-group label="Diagnosis:">
                  <b-form-input v-model="labRequest.diagnosis" required placeholder="Diagnosis"></b-form-input>
                </b-form-group>
              </b-col>
              <b-col cols="12" md="4">
                <b-form-group label="Gender:">
                  <b-form-select v-model="labRequest.gender" :options="genders" required></b-form-select>
                </b-form-group>
                <b-form-group label="Date of Birth:">
                  <b-form-input
                    v-model="labRequest.dataOfBirth"
                    required
                    placeholder="mm/dd/yyyy"
                    v-mask="'##/##/####'"
                  ></b-form-input>
                </b-form-group>
              </b-col>
            </b-row>
            <b-row>
              <b-col cols="12" md="8">
                <b-form-group label="Payment">
                  <b-form-radio-group
                    id="radio-group-payment"
                    v-model="labRequest.paymentType"
                    :options="paymentTypes"
                    name="radio-options"
                  ></b-form-radio-group>
                </b-form-group>
              </b-col>
              <b-col cols="12" md="4">
                <b-form-group label="Status:">
                  <b-form-select v-model="labRequest.status" :options="statuses" required></b-form-select>
                </b-form-group>
              </b-col>
            </b-row>
            <b-row>
              <b-col cols="12" md="12">
                <b-form-group label="HMO Type:" v-if="isHMO === true">
                  <b-form-select
                    v-model="labRequest.paymentName"
                    :options="hmoTypes"
                    @change="selectedHMOType(labRequest.paymentName)"
                    required
                  ></b-form-select>
                </b-form-group>
                <b-form-group label="Laboratories:">
                  <b-form-input list="list-laboratories"></b-form-input>
                  <datalist id="list-laboratories">
                    <option v-for="(name,index) in laboratories" :key="index">{{ name }}</option>
                  </datalist>
                </b-form-group>
                <b-form-textarea
                  id="textarea"
                  v-model="labRequest.laboratoryTextContent"
                  rows="4"
                ></b-form-textarea>
              </b-col>
            </b-row>
            <hr />
            <b-button-group>
              <b-button variant="success" type="submit">Preview</b-button>
              <b-button variant="danger" type="reset">Cancel</b-button>
            </b-button-group>
          </b-form>
        </b-container>
      </template>
    </b-modal>
    <i class="fa fa-clipboard" aria-hidden="true"></i> Laboratory Request
  </b-button>
</template>
<script>
const initLabRequest = {
  name: null,
  gender: null,
  diagnosis: null,
  dataOfBirth: null,
  status: null,
  paymentType: null,
  paymentName: null,
  laboratoryTextContent: null,
  recommendedLabs: []
};
import laboratories from "../../mock-data/laboratories";
export default {
  data() {
    return {
      labRequest: Object.assign({}, initLabRequest),
      hmoTypes: [
        { text: "Select", value: null },
        "MAXICARE",
        "CLINICCARD",
        "INTELLICARE",
        "MKAVI"
      ],
      genders: [{ text: "Select", value: null }, "Male", "Female"],
      statuses: [
        { text: "Select", value: null },
        "Neonate",
        "Infant",
        "Child",
        "Adolescent"
      ],
      paymentTypes: [
        { text: "OPD", value: "OPD" },
        { text: "HMO", value: "HMO" }
      ],
      laboratories,
      
    };
  },
  methods: {
    selectedHMOType: function(type) {
      this.labRequest.paymentName = type;
    },
    print: function() {},
    cancel: function() {
      this.$bvModal.hide("modal-lab-request");
      this.inPatient = Object.assign({}, initLabRequest);
    }
  },
  computed: {
    isHMO: function() {
      if (this.labRequest.paymentType) {
        if (this.labRequest.paymentType === "HMO") {
          return true;
        } else {
          this.labRequest.paymentName = "OPD";
          return false;
        }
      }
    }
  }
};
</script>